import React from 'react';

import { Link, useHistory, useLocation } from 'react-router-dom';

import logo from '../../assets/images/totem.png';
import '../../App.scss';
import { URLS } from '../../consts/urls';

export function NavWrapper({ children }) {
    const [nav, setNav] = React.useState(false);
    const toggleNav = () => setNav('');

    const navFunction = (array) => {
        setNav(array);
    }

    const history = useHistory();

    const solutionsOptions = [
        { label: 'Mixed Reality', link: `${URLS.MIXED_REALITY}`},
        { label: 'Metaverse', link: `${URLS.METAVERSE}`},
        { label: 'E-Learning', link: `${URLS.ELEARNING}`},
        { label: 'Serious Games', link: `${URLS.SERIOUS_GAMES}`},
        { label: 'Simulations', link: `${URLS.SIMULATIONS}`},
    ];

    const productsOptions = [
        { label: 'Unlock: Project Management', link: `${URLS.UNLOCK}`},
        { label: 'Smartup', link: `${URLS.SMARTUP}`},
        { label: 'Microlaunch Online', link: `${URLS.MICROLAUNCH}`},
        { label: 'GuideWeld VR', link: `${URLS.PRODUCTS}${URLS.GUIDEWELD}`},
    ];

    const linkFunction = (url) => {
        history.push(url);
        //window.location.reload();
    }

    const isCurrent = (url) => {
        return window.location.href.includes(url);
    }

    return (
    <>
        <div id="blocker" className="blocker"/>
        <div className="App" onMouseLeave={toggleNav}>
            <header className="App-header desktop-only">
                <Link className={ isCurrent(URLS.ABOUT_US) ? 'active' : null } onMouseEnter={toggleNav} onClick={() => linkFunction(URLS.ABOUT_US)}>About</Link>
                <Link className={ isCurrent(URLS.SOLUTIONS) ? 'active' : null }  onMouseEnter={() => navFunction(solutionsOptions)} onClick={() => linkFunction(URLS.SOLUTIONS)}>Solutions</Link>
                <img onMouseEnter={toggleNav} className="logo" src={logo} alt="logo" onClick={() => linkFunction(URLS.HOME)}/>
                <Link className={ isCurrent(URLS.PRODUCTS) ? 'active' : null }  onMouseEnter={() => navFunction(productsOptions)} onClick={() => linkFunction(URLS.PRODUCTS)}>Products</Link>
                <Link className={ isCurrent(URLS.BLOG) ? 'active' : null }  onMouseEnter={toggleNav} onClick={() => linkFunction(URLS.BLOG)}>Blog</Link>
            </header>
            <>
                <div className={nav ? "navbar" : "navbar-fade"}>
                { nav != "" ?
                    nav.map((item) => (
                        <Link className={ isCurrent(item.link) ? 'active' : null }  to={item.link} onClick={() => linkFunction(item.link)}>{item.label}</Link>
                    ))
                : null }
            </div>
            <div onMouseOver={toggleNav} className={nav ? "divider" : "divider-fade"}></div>
            </>
            {children}
        </div>

    </>
    );
}
